import React, { Component } from 'react';
import MonthPicker from './components/MonthPicker/MonthPicker';

class Home extends Component {
  render() {
    return (
      <div style={{ margin: '0' }}>
        <div style={{ margin: '0' }}>
          <MonthPicker locale="en" />
        </div>
      </div>
    );
  }
}

export default Home;



